.date-wrapper{
    p{
        margin: 0;
    }
}
.moment-list-wrapper{
    background-color: #f7f7f7;
    border-radius: 4px;
    height: calc(100vh - 568px);
    overflow: auto;
}
.momentItem{
    position: relative;
    background-color: #fff;
    padding: 10px;
    p{
        margin-top: 0;
        font-size: 0.9rem;
        color: #637381;
    }

    .moment-more-option-trigger{
        position: absolute;
        top: 4px;
        right: 4px;
    }
}
.my-moments{
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    color: #637381;
    margin-bottom: 16px;
}
.moment-type{
    background-color: rgba(0, 171, 85, 0.08);
    color: #00AB55;
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 0.75em;
    font-weight: 700;
    letter-spacing: 0.5px;
    display: inline-block;
    margin-bottom: 10px;
}