.view-action-container{
    display: flex;
    align-items: center;
    button{
        margin-right: 12px;
    }
}
.CalendarContainer{
    position: relative;
}
.calendar-actions-container{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
}
.calendar-actions-wrapper{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 0;
    button{
        font-size: 12px;
    }
}
.move-calendar-date-actions-wrapper{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    max-width: 234px;
}
.owner-detail-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}
.asignee-info{
    font-size: 0.9rem;
    display: flex;
    align-items: flex-start;
    .label{
        margin-right: 10px;
    }
}