.TaskViewContainer{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.todo-header-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.actions-container{
    display: flex;
    align-items: center;
    margin-right: 10px;
    li{
        display: flex;
        align-items: center;
        padding: 0 10px;
        margin-left: 20px;
        transition: all 300ms ease;
        cursor: pointer;
        border-radius: 4px;
        .text,
        svg path{
            transition: all 300ms ease;
        }
        &:hover{
            background: rgba(0, 171, 85, 0.08);
            .text{
                color: #00AB55;
            }
            svg path{
                fill: #00AB55;
            }
        }
    }
    .icon{
        width: 20px;
        margin-right: 10px;
        position: relative;
        &>svg{
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
    }
}