.NavigationContainer{
    padding: 20px 16px;
    flex: 0 0 220px;
    height: 100vh;
    box-shadow: 0 0px 6px rgba(0,0,0,0.16), 0 0px 6px rgba(0,0,0,0.23);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
    overflow: auto;
    background-color: #fff;
    ul{
        list-style: none;
    }
}
.nav-link{
    font-weight: 400;
    font-size: 14px;
    display: flex;
    align-items: center;
    color: #637381;
    text-decoration: none;
    padding: 0 12px;
    height: 48px;
    margin-bottom: 4px;
    border-radius: 8px;
    transition: all 300ms ease;
    cursor: pointer;
    path{
        transition: all 300ms ease;
    }
    &.active,
    &:hover{
        background: rgba(0, 171, 85, 0.08);
        color: #00AB55;
        path{
            fill: #00AB55;
        }
    }
}
.icon-container{
    width: 30px;
    margin-right: 8px;
}
.nav-logo-container{
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    p{
        margin-left: 14px;
        font-size: 18px;
    }
}
.nav-section-header,
.filter-by-heading{
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-transform: uppercase;
    color: #212B36;
    margin-bottom: 12px;
    padding: 0 12px;
}
.filter-by-heading{
    padding: 12px 0 0;
    margin-bottom: 6px;
    color: #5b5b5b;
}
.nav-section{
    margin-bottom: 28px;
}
.parent-navigation-wrapper{
    position: relative;
    .trigger{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        &.open{
            svg{
                transform: rotate(-180deg);
            }
        }
        svg{
            transition: all 300ms ease;
        }
    }
}
.SubNavigationContainer{
    font-weight: 400;
    font-size: 14px;
    align-items: center;
    color: #637381;
    font-family: 'Public Sans', sans-serif;
}
.tasks-filter-wrapper{
    padding-left: 16px;
    label span{
        font-size: 12px;
    }
    .MuiRadio-root,
    .MuiCheckbox-root{
        svg{
            width: 16px;
        }
    }
}